import { between, random } from './random.js';
export function generateMockData(length, seed) {
    const result = [];
    const gen = random(seed);
    for (let i = 0; i < length; i++) {
        result.push({
            label: `Iter: ${i}`,
            x: between(gen, 100, 300),
            y: between(gen, 400, 800),
            z: between(gen, 1000, 2000),
        });
    }
    return result;
}
